				"html": "<p>On Friday, I had the opportunity to go to the first LA Node.js Meetup. It was great. The presentors did a great job. I wrote down notes the best I could but unfortunately there are a few gaps.\n{: .first-paragraph}</p>\n<p>The first presentor was a CodeSmith student. His name is Jaret Fowler &#x26; he can be found <a href=\"//github.com/10000highfives\">here</a>. He started the project <a href=\"//webflight.io\">Webflight</a>. Webflight's goal is to use BitTorrent in a peer-to-peer format to allow users to host the content of that site. This would mean that as users of the site increased so would the speed of that site.</p>\n<h2>Cliff notes of Webflight</h2>\n<ul>\n<li>Way to share between browsers</li>\n<li>Browser on a server</li>\n<li>\n<p><a href=\"//electron.atom.io/\">Electron</a></p>\n<ul>\n<li><a href=\"//www.chromium.org/Home\">Chromium</a> + Node JS</li>\n<li>Browser, Render => Modules</li>\n</ul>\n</li>\n<li>\n<p>Downsides</p>\n<ul>\n<li>V8 = node &#x26; electron</li>\n<li>Memory management</li>\n<li>mis-using electron</li>\n<li>torrent version</li>\n</ul>\n</li>\n</ul>\n<p>The second presentor discussed Nodebots. He described why nodejs made building robotics easier (My lack of notes for his presentation has nothing to do with the quality of the presentation). </p>\n<p>The third presentor, <a href=\"//yunong.io/\">Yunong Xiao</a>, discsussed <em>node in production</em>; especially debugging it. He briefly summarized how the product is set up &#x26; then dug into how he &#x26; his team went into debugging issues using their node product.</p>\n<h2>Cliff notes of Node in production</h2>\n<p><strong>Summary</strong></p>\n<ul>\n<li>Website runs on node</li>\n<li>Built with react</li>\n<li>Dynamic asset manager</li>\n</ul>\n<h2>Debugging</h2>\n<p><em>lets work the problem &#x26; not guessing (Apollo 13)</em></p>\n<ul>\n<li>\n<p>Runtime performance</p>\n<ul>\n<li>Restify</li>\n</ul>\n</li>\n<li>\n<p>Runtime crashes</p>\n<ul>\n<li>stack trace</li>\n<li>flame graph</li>\n</ul>\n</li>\n<li>\n<p>Memory leaks</p>\n<ul>\n<li>core dump</li>\n<li>postmortem</li>\n<li>debug</li>\n<li>restart app</li>\n</ul>\n</li>\n</ul>",
